<template>
    <carousel class="col-12 px-0 position-relative owl-carousel-container category" 
        :dots="false" 
        :lazyLoad="true" 
        :nav="false" 
        :responsive="{ 0:{items:2,nav:false,stagePadding:0}, 768:{items:4.,nav:false,stagePadding:60}, 992:{items:6,nav:false,stagePadding:80} }">

        <template slot="prev"><span class="prev nav-button"> <img src="@/assets/icons/left-carousel.svg"> </span></template>

        <router-link :key="index" v-for="(category, index) in categories" :to="{ name: 'category', params: { gender: gender, category: category.slug }}">
            <b-img-lazy :blank-color="'#bbb'" loading="lazy" :src="category.img.url" :alt="category.img.alt" />
            <div class="overlay position-absolute d-flex justify-content-center align-items-end">
                <h1 class="text-center">{{ category.name | capitalize}}</h1>
            </div>
        </router-link>

        <template slot="next"><span class="next nav-button">  <img src="@/assets/icons/right-carousel.svg">  </span></template>
    </carousel>
</template>
<script>
import carousel from 'v-owl-carousel' 
 
export default {
    name: 'CarouselCategories',
    props: {
        gender:{
            type: String,
            required: true
        },
        categories:{
            type: Array,
            default: () => [],
            required: true
        }
    },
    components: {carousel}
}
</script>
<style>
.owl-carousel-container.category .owl-carousel .owl-item {
    padding: 20px 5px 30px;
}
.owl-carousel-container.category .owl-item img{
    -webkit-box-box-shadow: 0px 0px 0px 0px #fff;
    -moz-box-shadow: 0px 0px 0px 0px #fff;
    box-shadow: 0px 0px 0px 0px #fff;
    border-radius: 5px;
    transition: box-shadow .3s;
}
.owl-carousel-container.category .owl-item:hover img{
    -webkit-box-shadow: 0px 0px 12px 2px rgb(177 186 204);
    -moz-box-shadow: 0px 0px 12px 2px rgb(177 186 204);
    box-shadow: 0px 0px 12px 2px rgb(177 186 204);
}
.owl-carousel-container.category .owl-carousel .owl-item a .overlay{
    top: 20px;
    width: calc(100% - 10px);
    height: calc(100% - 50px);
    background: #35495e40;
}
.owl-carousel-container.category .owl-carousel .owl-item a h1{
    color: #fff;
    font-weight: bold;
    font-size: 20px;
    max-width: 100%;
    text-shadow: 0px 0px 10px rgba(0,0,0);
    transition: font-size .3s;
}
.owl-carousel-container.category .owl-carousel .owl-item:hover a h1{
    font-size: 24px;
}
</style>